
.table th.sortable {
    cursor: pointer;
    user-select: none;
}

.table th.sortable:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.table th {
    position: relative;
    padding-right: 2rem;
}

.table th .mdi {
    position: absolute;
    right: 0.5rem;
    top: 50%;
    transform: translateY(-50%);
}
