
.nautobot-vue-app {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.nautobot-vue-app .container-fluid {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
    opacity: 0;
}
